.NavigationItem {
	margin: 0;
	width: 100%;
}

.NavigationItem a {
	padding: 15px 25px;
	color: black;
	text-decoration: none;
	width: 100%;
	box-sizing: border-box;
	display: block;
	transition: color 1.2s;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
	color: hsl(352°, 100%, 85%);
	// color: hsl(133°, 100%, 79%);
}

@media (min-width: 500px) {
	.NavigationItem {
		margin: 0;
		display: flex;
		height: 100%;
		align-items: center;
		width: auto;
	}

	.NavigationItem a {
		color: rgb(230, 230, 230);
		height: 100%;
		padding: 16px 10px;
		border-bottom: 4px solid transparent;
	}

	.NavigationItem a:hover,
	.NavigationItem a:active,
	.NavigationItem a.active {
		color: #14ffec;
	}
}
